export default {"ts":"import { Attributes, Component } from 'jinge';\n\nimport _tpl from './disable.html';\n\nexport default class ExampleDatepickerDisable extends Component {\n  static template = _tpl;\n\n  selectedDate: Date;\n\n  constructor(attrs: Attributes) {\n    super(attrs);\n    this.selectedDate = new Date('2018-07-12');\n  }\n\n  isHoliday(year: number, month: number, date: number, weekday: number) {\n    return weekday === 6 || weekday === 0;\n  }\n}\n","sass":".demo {\n  p {\n    display: flex;\n    flex-wrap: wrap;\n  }\n\n  div {\n    flex: 1;\n    min-width: 120px;\n    margin: 0 6px;\n  }\n}\n","html":"<!-- import style from './disable.scss' -->\n<div :class=\"style.demo\">\n  <p>\n    <div>\n      <md-datepicker\n        e:value=\"selectedDate\"\n        disabled\n      />\n    </div>\n    <div>\n      <md-datepicker\n        e:value=\"selectedDate\"\n        e:disabledDates=\"isHoliday\"\n        on:confirm=\"selectedDate = args[0]\"\n      />\n    </div>\n  </p>\n</div>"}