export default {"ts":"import { Attributes, Component } from 'jinge';\n\nimport _tpl from './basic.html';\n\nexport default class ExampleDatepickerBasic extends Component {\n  static template = _tpl;\n\n  selectedDate: Date;\n\n  constructor(attrs: Attributes) {\n    super(attrs);\n    this.selectedDate = null;\n  }\n\n  onChange(v: Date) {\n    this.selectedDate = v;\n    // eslint-disable-next-line no-console\n    console.log(v);\n  }\n}\n","sass":".demo {\n  p {\n    display: flex;\n    flex-wrap: wrap;\n  }\n\n  div {\n    flex: 1;\n    min-width: 120px;\n    margin: 0 6px;\n  }\n}\n","html":"<!-- import style from './basic.scss' -->\n<div :class=\"style.demo\">\n  <p>\n    <div>\n      <md-datepicker\n        e:value=\"selectedDate\"\n        on:confirm=\"onChange\"\n      />\n    </div>\n    <div>\n      <md-datepicker\n        e:value=\"selectedDate\"\n        on:confirm=\"onChange\"\n        e:showDefaultPlaceholder=\"false\"\n      />\n    </div>\n    <div>\n      <md-datepicker\n        e:value=\"selectedDate\"\n        on:confirm=\"onChange\"\n        placeholder=\"请选择日期\"\n      />\n    </div>\n  </p>\n  <p>\n    <div>\n      <md-datepicker\n        e:value=\"selectedDate\"\n        on:confirm=\"onChange\"\n      >\n        <label>选择日期</label>\n      </md-datepicker>\n    </div>\n    <div>\n      <md-datepicker\n        e:value=\"selectedDate\"\n        on:confirm=\"onChange\"\n        e:showDefaultPlaceholder=\"false\"\n      >\n        <label>选择日期</label>\n      </md-datepicker>\n    </div>\n    <div>\n      <md-datepicker\n        e:value=\"selectedDate\"\n        on:confirm=\"onChange\"\n        placeholder=\"请选择日期\"\n      >\n        <label>选择日期</label>\n      </md-datepicker>\n    </div>\n  </p>  \n</div>"}